@use '../util' as *;

.card-grid{
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: repeat(2, auto); // auto only takes the height of the item
    justify-items: start;
    gap : rem(23);

    .card__count{
        margin-bottom: 0;
    }

    .card__count, .card__change{
        align-self: end;
    }
    img, 
    .card__change{
        justify-self: end;
    }
}